import React from 'react'
import styles from './styles'

const Header = () => (
  <header style={styles.container}>
    <span style={{ opacity: 0.5 }}>&lt;</span>Developing Web-Based Start-Ups<span style={{ opacity: 0.35 }}>/&gt;</span>
  </header>
)

export default Header



